export interface Bill {
  id: string;
  name: string;
  value: number;
  fileUrl?: string;
}

export interface Invoice {
  id: string;
  name: string;
  value: number;
  fileUrl?: string;
  bills: Bill[];
}

export interface Company {
  id: string;
  name: string;
  poNumber: string;
  poValue: number;
  billNumber: string;
  invoices: Invoice[];
}

export interface Floor {
  id: string;
  name: string;
  totalCost: number;
  amountPaid: number;
  companies: Company[];
}

export interface Building {
  id: string;
  name: string;
  floors: Floor[];
}

