// Helper function to calculate building payment percentage
export function calculateBuildingPaymentPercentage(building: Building): number {
  const totalCost = building.floors.reduce((sum, floor) => sum + floor.totalCost, 0);
  const totalPaid = building.floors.reduce((sum, floor) => sum + floor.amountPaid, 0);
  
  if (totalCost === 0) return 0;
  return Math.round((totalPaid / totalCost) * 100);
}

// Helper function to calculate floor payment percentage
export function calculateFloorPaymentPercentage(floor: Floor): number {
  if (floor.totalCost === 0) return 0;
  return Math.round((floor.amountPaid / floor.totalCost) * 100);
}

// Helper function to calculate company total amount from all invoices
export function calculateCompanyTotalAmount(company: Company): number {
  return company.invoices.reduce((sum, invoice) => {
    return sum + invoice.bills.reduce((billSum, bill) => billSum + bill.value, 0);
  }, 0);
}

// Helper function to find a specific floor
export function findFloor(buildingId: string, floorId: string): Floor | null {
  const building = mockBuildings.find(b => b.id === buildingId);
  if (!building) return null;
  return building.floors.find(f => f.id === floorId) || null;
}
