'use client';

import { useParams } from "next/navigation";
import { CompaniesDashboard } from "../../../../../components/CompaniesDashboard";

export default function FloorPage() {
  const params = useParams();

  // FIX: We add (|| "") to ensure it is never undefined.
  // We also handle the case where it might be an array (rare, but possible).
  const buildingId = Array.isArray(params.buildingId) 
    ? params.buildingId[0] 
    : (params.buildingId || "");

  const floorId = Array.isArray(params.floorId) 
    ? params.floorId[0] 
    : (params.floorId || "");

  return (
    <main>
      <CompaniesDashboard 
        buildingId={buildingId} 
        floorId={floorId} 
      />
    </main>
  );
}