"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { ArrowLeft, ChevronDown, ChevronUp, Upload, Plus } from "lucide-react";
import { findFloor, calculateCompanyTotalAmount, Floor, Company, Invoice, Bill } from "../data/mockData";

interface CompaniesDashboardProps {
  buildingId: string;
  floorId: string;
}

export function CompaniesDashboard({ buildingId, floorId }: CompaniesDashboardProps) {
  const router = useRouter();
  const [floor, setFloor] = useState<Floor | null>(null);
  const [expandedCompanies, setExpandedCompanies] = useState<Set<string>>(new Set());
  const [expandedInvoices, setExpandedInvoices] = useState<Set<string>>(new Set());
  
  // Dialog states
  const [showAddCompanyDialog, setShowAddCompanyDialog] = useState(false);
  const [showAddInvoiceDialog, setShowAddInvoiceDialog] = useState(false);
  const [showAddBillDialog, setShowAddBillDialog] = useState(false);
  const [selectedCompanyId, setSelectedCompanyId] = useState<string | null>(null);
  const [selectedInvoiceId, setSelectedInvoiceId] = useState<string | null>(null);

  // Form states
  const [newCompanyName, setNewCompanyName] = useState("");
  const [newCompanyPO, setNewCompanyPO] = useState("");
  const [newCompanyPOValue, setNewCompanyPOValue] = useState("");
  const [newCompanyBillNo, setNewCompanyBillNo] = useState("");

  const [newInvoiceName, setNewInvoiceName] = useState("");
  const [newInvoiceValue, setNewInvoiceValue] = useState("");

  const [newBillName, setNewBillName] = useState("");
  const [newBillValue, setNewBillValue] = useState("");

  useEffect(() => {
    const foundFloor = findFloor(buildingId, floorId);
    if (foundFloor) {
      setFloor(foundFloor);
    }
  }, [buildingId, floorId]);

  const toggleCompanyExpanded = (companyId: string) => {
    const newExpanded = new Set(expandedCompanies);
    if (newExpanded.has(companyId)) {
      newExpanded.delete(companyId);
    } else {
      newExpanded.add(companyId);
    }
    setExpandedCompanies(newExpanded);
  };

  const toggleInvoiceExpanded = (invoiceId: string) => {
    const newExpanded = new Set(expandedInvoices);
    if (newExpanded.has(invoiceId)) {
      newExpanded.delete(invoiceId);
    } else {
      newExpanded.add(invoiceId);
    }
    setExpandedInvoices(newExpanded);
  };

  const handleAddCompany = (e: React.FormEvent) => {
    e.preventDefault();
    if (newCompanyName.trim() && newCompanyPO.trim() && newCompanyPOValue && newCompanyBillNo.trim() && floor) {
      const newCompany: Company = {
        id: Date.now().toString(),
        name: newCompanyName.trim(),
        poNumber: newCompanyPO.trim(),
        poValue: parseFloat(newCompanyPOValue),
        billNumber: newCompanyBillNo.trim(),
        invoices: []
      };
      
      setFloor({
        ...floor,
        companies: [...floor.companies, newCompany]
      });
      
      setNewCompanyName("");
      setNewCompanyPO("");
      setNewCompanyPOValue("");
      setNewCompanyBillNo("");
      setShowAddCompanyDialog(false);
    }
  };

  const handleAddInvoice = (e: React.FormEvent) => {
    e.preventDefault();
    if (newInvoiceName.trim() && newInvoiceValue && selectedCompanyId && floor) {
      const newInvoice: Invoice = {
        id: Date.now().toString(),
        name: newInvoiceName.trim(),
        value: parseFloat(newInvoiceValue),
        bills: []
      };
      
      const updatedCompanies = floor.companies.map(company => {
        if (company.id === selectedCompanyId) {
          return {
            ...company,
            invoices: [...company.invoices, newInvoice]
          };
        }
        return company;
      });
      
      setFloor({
        ...floor,
        companies: updatedCompanies
      });
      
      setNewInvoiceName("");
      setNewInvoiceValue("");
      setSelectedCompanyId(null);
      setShowAddInvoiceDialog(false);
    }
  };

  const handleAddBill = (e: React.FormEvent) => {
    e.preventDefault();
    if (newBillName.trim() && newBillValue && selectedCompanyId && selectedInvoiceId && floor) {
      const newBill: Bill = {
        id: Date.now().toString(),
        name: newBillName.trim(),
        value: parseFloat(newBillValue)
      };
      
      const updatedCompanies = floor.companies.map(company => {
        if (company.id === selectedCompanyId) {
          return {
            ...company,
            invoices: company.invoices.map(invoice => {
              if (invoice.id === selectedInvoiceId) {
                return {
                  ...invoice,
                  bills: [...invoice.bills, newBill]
                };
              }
              return invoice;
            })
          };
        }
        return company;
      });
      
      setFloor({
        ...floor,
        companies: updatedCompanies
      });
      
      setNewBillName("");
      setNewBillValue("");
      setSelectedCompanyId(null);
      setSelectedInvoiceId(null);
      setShowAddBillDialog(false);
    }
  };

  const handleFileUpload = (type: 'invoice' | 'bill', id: string) => {
    // Mock file upload - in real app would handle actual file upload
    alert(`Upload ${type} file for ${id}`);
  };

  if (!floor) {
    return (
      <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="text-center">
          <h2 className="mb-4">Floor not found</h2>
          <button
            onClick={() => router.push(`/building/${buildingId}`)}
            className="px-4 py-2 text-white bg-blue-600 rounded-md hover:bg-blue-700"
          >
            Return to Building
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-50">
      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* Header */}
        <div className="mb-8">
          <button
            onClick={() => router.push(`/building/${buildingId}`)}
            className="flex items-center text-blue-600 hover:text-blue-700 mb-4"
          >
            <ArrowLeft className="w-4 h-4 mr-2" />
            Back to Floors
          </button>
          
          <div className="flex items-center justify-between">
            <h1 className="font-semibold text-gray-700">{floor.name}</h1>
            <button
              onClick={() => setShowAddCompanyDialog(true)}
              className="px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 flex items-center gap-2"
            >
              <Plus className="w-4 h-4" />
              Add Company
            </button>
          </div>
        </div>

        {/* Companies Table */}
        <div className="bg-white rounded-lg shadow overflow-hidden mb-6">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-gray-50 border-b border-gray-200">
                <tr>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">COMPANY</th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">PO NO</th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">PO VALUE</th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">BILL NO.</th>
                  <th className="px-6 py-3 text-left text-sm font-semibold text-gray-900">TOTAL AMOUNT</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200">
                {floor.companies.length === 0 ? (
                  <tr>
                    <td colSpan={5} className="px-6 py-12 text-center text-gray-500">
                      No companies added yet. Click "Add Company" to get started.
                    </td>
                  </tr>
                ) : (
                  floor.companies.map((company) => (
                    <tr key={company.id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 font-medium text-gray-900">{company.name}</td>
                      <td className="px-6 py-4 text-gray-700">{company.poNumber}</td>
                      <td className="px-6 py-4 text-gray-700">${company.poValue.toLocaleString()}</td>
                      <td className="px-6 py-4 text-gray-700">{company.billNumber}</td>
                      <td className="px-6 py-4 text-gray-700">${calculateCompanyTotalAmount(company).toLocaleString()}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        </div>

        {/* Detailed Invoice and Bill Sections */}
        {floor.companies.map((company) => (
          <div key={company.id} className="bg-white rounded-lg shadow font-semibold text-gray-500">
            <div className="px-6 py-4 border-b border-gray-200">
              <div className="flex items-center justify-between">
                <h2 className="mb-0">{company.name}</h2>
                <button
                  onClick={() => {
                    setSelectedCompanyId(company.id);
                    setShowAddInvoiceDialog(true);
                  }}
                  className="px-3 py-1.5 bg-blue-600 text-white rounded-md hover:bg-blue-700 text-sm flex items-center gap-2"
                >
                  <Plus className="w-3 h-3" />
                  Add Invoice
                </button>
              </div>
            </div>
            
            <div className="p-6 space-y-4">
              {company.invoices.length === 0 ? (
                <p className="text-gray-500 text-center py-4">No invoices added yet</p>
              ) : (
                company.invoices.map((invoice) => (
                  <div key={invoice.id} className="border border-gray-200 rounded-lg">
                    {/* Invoice Header */}
                    <div className="flex items-center justify-between p-4 bg-gray-50">
                      <div className="flex items-center gap-4 flex-1">
                        <button
                          onClick={() => toggleInvoiceExpanded(invoice.id)}
                          className="flex items-center gap-2 text-gray-700 hover:text-gray-900"
                        >
                          {expandedInvoices.has(invoice.id) ? (
                            <ChevronUp className="w-4 h-4" />
                          ) : (
                            <ChevronDown className="w-4 h-4" />
                          )}
                          <span className="font-medium">{invoice.name}</span>
                        </button>
                        <span className="text-gray-600">${invoice.value.toLocaleString()}</span>
                      </div>
                      <button
                        onClick={() => handleFileUpload('invoice', invoice.id)}
                        className="px-3 py-1.5 bg-gray-600 text-white rounded-md hover:bg-gray-700 text-sm flex items-center gap-2"
                      >
                        <Upload className="w-3 h-3" />
                        Upload Invoice
                      </button>
                    </div>

                    {/* Bills List (Expandable) */}
                    {expandedInvoices.has(invoice.id) && (
                      <div className="p-4 space-y-2">
                        {invoice.bills.map((bill) => (
                          <div key={bill.id} className="flex items-center justify-between p-3 bg-gray-50 rounded">
                            <span className="text-gray-700">{bill.name}</span>
                            <div className="flex items-center gap-4">
                              <span className="text-gray-600">${bill.value.toLocaleString()}</span>
                              <button
                                onClick={() => handleFileUpload('bill', bill.id)}
                                className="px-3 py-1 bg-gray-600 text-white rounded text-sm flex items-center gap-2"
                              >
                                <Upload className="w-3 h-3" />
                                Upload Bill
                              </button>
                            </div>
                          </div>
                        ))}
                        <button
                          onClick={() => {
                            setSelectedCompanyId(company.id);
                            setSelectedInvoiceId(invoice.id);
                            setShowAddBillDialog(true);
                          }}
                          className="w-full p-3 border-2 border-dashed border-gray-300 rounded text-gray-600 hover:border-blue-400 hover:text-blue-600 flex items-center justify-center gap-2"
                        >
                          <Plus className="w-4 h-4" />
                          Add Bill
                        </button>
                      </div>
                    )}
                  </div>
                ))
              )}
            </div>
          </div>
        ))}
      </div>

      {/* Add Company Dialog */}
      {showAddCompanyDialog && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl p-6 w-full max-w-md">
            <h2 className="mb-4">Add New Company</h2>
            <form onSubmit={handleAddCompany}>
              <div className="mb-4">
                <label htmlFor="companyName" className="block text-sm font-medium text-gray-700 mb-2">
                  Company Name
                </label>
                <input
                  type="text"
                  id="companyName"
                  value={newCompanyName}
                  onChange={(e) => setNewCompanyName(e.target.value)}
                  placeholder="e.g., BASE AREA"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  autoFocus
                />
              </div>
              
              <div className="mb-4">
                <label htmlFor="poNumber" className="block text-sm font-medium text-gray-700 mb-2">
                  PO Number
                </label>
                <input
                  type="text"
                  id="poNumber"
                  value={newCompanyPO}
                  onChange={(e) => setNewCompanyPO(e.target.value)}
                  placeholder="e.g., PO-2024-001"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div className="mb-4">
                <label htmlFor="poValue" className="block text-sm font-medium text-gray-700 mb-2">
                  PO Value ($)
                </label>
                <input
                  type="number"
                  id="poValue"
                  value={newCompanyPOValue}
                  onChange={(e) => setNewCompanyPOValue(e.target.value)}
                  placeholder="0.00"
                  min="0"
                  step="0.01"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>

              <div className="mb-4">
                <label htmlFor="billNumber" className="block text-sm font-medium text-gray-700 mb-2">
                  Bill Number
                </label>
                <input
                  type="text"
                  id="billNumber"
                  value={newCompanyBillNo}
                  onChange={(e) => setNewCompanyBillNo(e.target.value)}
                  placeholder="e.g., BILL-001"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              
              <div className="flex gap-3 justify-end">
                <button
                  type="button"
                  onClick={() => {
                    setShowAddCompanyDialog(false);
                    setNewCompanyName("");
                    setNewCompanyPO("");
                    setNewCompanyPOValue("");
                    setNewCompanyBillNo("");
                  }}
                  className="px-4 py-2 text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 text-white bg-blue-600 rounded-md hover:bg-blue-700"
                >
                  Add Company
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Add Invoice Dialog */}
      {showAddInvoiceDialog && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl p-6 w-full max-w-md">
            <h2 className="mb-4">Add New Invoice</h2>
            <form onSubmit={handleAddInvoice}>
              <div className="mb-4">
                <label htmlFor="invoiceName" className="block text-sm font-medium text-gray-700 mb-2">
                  Invoice Name
                </label>
                <input
                  type="text"
                  id="invoiceName"
                  value={newInvoiceName}
                  onChange={(e) => setNewInvoiceName(e.target.value)}
                  placeholder="e.g., Invoice 1"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  autoFocus
                />
              </div>
              
              <div className="mb-4">
                <label htmlFor="invoiceValue" className="block text-sm font-medium text-gray-700 mb-2">
                  Invoice Value ($)
                </label>
                <input
                  type="number"
                  id="invoiceValue"
                  value={newInvoiceValue}
                  onChange={(e) => setNewInvoiceValue(e.target.value)}
                  placeholder="0.00"
                  min="0"
                  step="0.01"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              
              <div className="flex gap-3 justify-end">
                <button
                  type="button"
                  onClick={() => {
                    setShowAddInvoiceDialog(false);
                    setNewInvoiceName("");
                    setNewInvoiceValue("");
                    setSelectedCompanyId(null);
                  }}
                  className="px-4 py-2 text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 text-white bg-blue-600 rounded-md hover:bg-blue-700"
                >
                  Add Invoice
                </button>
              </div>
            </form>
          </div>
        </div>
      )}

      {/* Add Bill Dialog */}
      {showAddBillDialog && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white rounded-lg shadow-xl p-6 w-full max-w-md">
            <h2 className="mb-4">Add New Bill</h2>
            <form onSubmit={handleAddBill}>
              <div className="mb-4">
                <label htmlFor="billName" className="block text-sm font-medium text-gray-700 mb-2">
                  Bill Name
                </label>
                <input
                  type="text"
                  id="billName"
                  value={newBillName}
                  onChange={(e) => setNewBillName(e.target.value)}
                  placeholder="e.g., Bill 1"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  autoFocus
                />
              </div>
              
              <div className="mb-4">
                <label htmlFor="billValue" className="block text-sm font-medium text-gray-700 mb-2">
                  Bill Value ($)
                </label>
                <input
                  type="number"
                  id="billValue"
                  value={newBillValue}
                  onChange={(e) => setNewBillValue(e.target.value)}
                  placeholder="0.00"
                  min="0"
                  step="0.01"
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                />
              </div>
              
              <div className="flex gap-3 justify-end">
                <button
                  type="button"
                  onClick={() => {
                    setShowAddBillDialog(false);
                    setNewBillName("");
                    setNewBillValue("");
                    setSelectedCompanyId(null);
                    setSelectedInvoiceId(null);
                  }}
                  className="px-4 py-2 text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200"
                >
                  Cancel
                </button>
                <button
                  type="submit"
                  className="px-4 py-2 text-white bg-blue-600 rounded-md hover:bg-blue-700"
                >
                  Add Bill
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}
